import React from 'react';

import styles from './header.module.scss';

export default class Header extends React.PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      isColorChanged: false,
      isTextHidden: true
    }

    this.changeColorClickHandler = this.changeColorClickHandler.bind(this);
    this.toggleTextClickHandler = this.toggleTextClickHandler.bind(this);
  }

  changeColorClickHandler() {
    this.setState((state) => ({
      isColorChanged: !state.isColorChanged
    }))
  }

  toggleTextClickHandler() {
    this.setState((state) => ({
      isTextHidden: !state.isTextHidden
    }))
  }

  render() {
    const {isColorChanged, isTextHidden} = this.state;
    const {headerText} = this.props;
    const {header, header__title, header__test, header__titleChangeColor} = styles;

    return (
      <div className={header}>
        <button type="button" onClick={this.changeColorClickHandler}>Change title color</button>
        <h1 className={isColorChanged ? [header__title, header__titleChangeColor].join(' ') : header__title}>{headerText}</h1>
        <span className={header__test}>Test title</span>
        <button type="button" onClick={this.toggleTextClickHandler}>Toggle text</button>
        {isTextHidden ? undefined : <span>Hidden text</span>}
      </div>
    );
  }
}